body,
.content {
  font-family: var(--bodyFont);
  color: var(--darkgrey);

  a {
    @include link-text;
    color: var(--accent-2);
    &:visited {
      color: var(--accent-2);
    }
    &:focus {
      outline: 3px solid var(--accent-2);
    }
  }
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--headingFont);
  color: var(--black);
  margin: 0;
}